        background-color : black;

    }
    .hero img {
        width: 100%;
        height: 100%;
        object-fit: cover;

    }
    .hero-text {
        position : absolute;
        top : 50%;
        left : 50%;
        transform : translate(-50%, -50%);
        color : white;
        font-weight : bold;
        text-shadow : 2 px 2 px 4 px rgba(0, 0, 0, 0.7);
    }
    .shop-now {
